body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.fullpage{
    height: 100vh;
    width: calc(100vw - (100vw - 100%));
    display: -webkit-flex;
    display: flex;
    -webkit-flex:auto;
            flex:auto;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;   
    padding-top: 50px; 
  }

.first{
    background-color:#00a0af;
    color: #000;
}
h1{
  font-size: 2.7rem
}
h2{
  padding: 25px
}
.first a{
  margin: 0 5px
}
.second{
    background-color: #3fb9c4;
}
h3{
    font-size: 3rem;
    margin-top:-30px
}
p{
    padding: 40px;
    margin: 0 auto;
    text-align: center;
    max-width: 750px;
    font-size: 1.2rem;
}

.card{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  /* align-items: flex-start; */
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 220px;
  width: 220px;
  background-color: #fff;
  margin:40px 10px;
  box-shadow: 0 1px 3px rgba(0,0,0,.12), 0 1px 2px rgba(0,0,0,.2);
  -webkit-transition: all 0.3s cubic-bezier(.25,.8,.25,1);
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
  
}
.card:hover{
  box-shadow: 0 10px 20px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,0.3);
}
.image-wrapper{
  height: 130px;
  width: 130px;
  margin: 0 auto;
}
.skill-title-wrapper{
  padding: 5px;
  font-size: 1.4rem
}

.third{
    background-color: #a5dfe4;
}  
.card-wrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
}
html,
body{
  height: 100%;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: 15px;
}
*{
  margin: 0;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.section{position: relative;}
.navigation{
  position: fixed;
  z-index: 2;
  text-align: right;
  height: 50px;
  top: 0;
  right: 0;
  left:0;
  background: rgba(255,255,255,0.25);
  line-height: 50px;
  font-size: 1.4rem;
  border-bottom: 1px solid rgba(255,255,255,0.3);
  cursor: pointer;
  color: #3c2f2f
}
.nav{
  float:right;
}
.nav a{
  padding: 12px 20px;
}
.nav a:hover{
  background: rgba(255,255,255,0.7)
}
.setting{
  float: left;
  padding: 0px 20px;
  font-size: 1.3rem;
}
.setting:hover{
  background: rgba(255,255,255,0.7)
}
.element{
  height: 0px;
  background: red
}
.icons-wrapper{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.icon{
  margin: 10px;
}


.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

